import React from 'react';
import Samayaraj from '../../assets/Samayaraj.webp';
import Sathish from '../../assets/Satish.webp';

const EventGuest = () => {
  const guests = [
    {
      name: 'Mr.Samayaraj',
      title: 'Co-Founder',
      company: 'Unibose Technology',
      photo: Samayaraj,
      description: 'Samayaraj is the Co-Founder of Unibose Technology, specializing in advanced robotics and automation.',
    },
    {
      name: 'Mr.Sathish',
      title: 'Founder & CEO',
      company: 'Otomatiks',
      photo: Sathish,
      description: 'Sathish an Educationist is the Founder & CEO of Otomatiks, a leading firm in innovative education solutions.',
    },
  ];

  return (
    <div className="flex flex-col items-center py-12 px-4 md:px-20 lg:px-32" style={{ fontFamily: 'Poppins, sans-serif' }}>
      <h1 className="text-4xl lg:text-5xl font-bold text-purple-600 text-center">Chief Guest / Key Notes Speaker</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-12">
        {guests.map((guest, index) => (
          <div
            key={index}
            className="relative  bg-white rounded-lg p-6"
          >
            <img
              src={guest.photo}
              alt={guest.name}
              className="w-40 h-40 rounded-full mx-auto mb-4 transition-transform duration-500"
            />
            <h2 className="text-3xl font-bold mb-2 text-center text-gray-900">{guest.name}</h2>
            <h3 className="text-lg text-gray-800 mb-2 text-center">
              {guest.title} at <span className="text-gray-900">{guest.company}</span>
            </h3>
            <p className="text-gray-800 text-center">{guest.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default EventGuest;
